<template>
	<card-popup>
		<div slot="header">
			 
			<button type="button" v-on:click="$emit('close')">&times;</button>
		</div>
		<div slot="body">
			<input type="text" placeholder=" ." v-model="subject">
			<textarea name="" id="" cols="30" rows="10" placeholder=" ." v-model="content"></textarea>
		</div>
		<div slot="footer" class="text-right">
			<button class="modal-default-button" v-on:click="[$emit('close'), submitMemoList()]"></button>
		</div>
	</card-popup>
</template>

<script>
import CardPopup from '@/components/memo/CardPopup.vue';
import Bus from '@/utils/bus.js'

export default {
	components: {
		CardPopup
	},
	data() {
		return {
			showModal: false,
			subject: '',
			content: ''
		}
	},
	methods: {
		submitMemoList() {
			// API (insert)
			this.$http({
				method: 'post',
				url: '/memo/create',
				data: {
					subject: this.subject,
					content: this.content,
					pos: 65335
				}
			})
			.then(function(response) {
				console.log('!', response);
				Bus.$emit('onFetch');
			})
			.catch(function(err) {
				console.log('    : ', err);
			})
		}
	}

}
</script>

